.overlay {
  opacity: 0;
  transition: 0.5s ease;
}
.overlayFileName:hover .overlay {
  opacity: 1;
}

.fn {
  position: absolute;
  bottom: 0;
  width: 200px;
  color: #fff;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.8);
  padding-block: 6px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
}

.del {
  position: absolute;
  top: 7px;
  left: 190px;
  color: #fff;
  padding: 5px;
  border-radius: 5px;
  padding-block: 7px;
  background-color: rgba(220, 37, 69, 0.9);
  cursor: pointer;
}

.del-modal {
  position: absolute;
  top: 10px;
  right: 50px;
  color: #fff;
  width: 20px;
  height: 20px;
  padding: 5px;
  border-radius: 5px;
  padding-block: 7px;
  background-color: rgba(220, 37, 69, 0.9);
  cursor: pointer;
}

.clo-modal {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 20px;
  height: 20px;
  color: #fff;
  padding: 5px;
  border-radius: 5px;
  padding-block: 7px;
  background-color: rgba(0, 0, 0, 0.9);
  cursor: pointer;
  border-radius: 10px;
}

.fn-modal {
  position: absolute;
  bottom: 10px;
  width: 100%;
  color: #fff;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.8);
  padding-block: 12px;
  height: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
}

.align-cent {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 13px;
  font-weight: 600;
  flex-direction: column;
  position: absolute;
  /* z-index: 1; */
}

.mo {
  height: 100%;
  width: 100%;
  font-size: 13px;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.1);
}
