.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.body-holder {
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo-style {
  margin: 10px;
  margin-bottom: 30px;
  padding: 10px;
  width: 200px;
  height: 100px;
}

.loginHolder {
  padding: 30px;
  min-width: 400px;
  min-height: 400px;
  border-radius: 20px;
  background: #e0e0e0;
  box-shadow: 20px 20px 60px #bebebe, -20px -20px 60px #ffffff;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
}

.login-form-forgot {
  font-weight: 700;
}
