@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap");

body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 600;
  background-color: #fff !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.login-form {
  max-width: 300px;
  min-width: 300px;
  width: 300px;
}

.ant-checkbox-wrapper {
  float: left;
  /* font-weight: 500 !important; */
}

.login-form-forgot {
  float: right;
  /* font-weight: 700 !important; */
}
.ant-col-rtl .login-form-forgot {
  float: left;
}
.login-form-button {
  width: 100%;
  border-radius: 8px !important;
}

.ant-input-affix-wrapper {
  border-radius: 8px !important;
}

.ant-input {
  line-height: 2;
  border-radius: 8px !important;
  font-weight: 500 !important;
}

.ant-input-affix-wrapper > input.ant-input {
  font-weight: 600 !important;
  font-size: 14px;
}

.ant-form-item-explain-error {
  margin: 10px;
}
.ant-btn-lg {
  font-weight: 600 !important;
  font-size: 14px;
}

.ant-btn {
  font-weight: 600 !important;
  font-size: 14px !important;
  border-radius: 8px !important;
}

.ModalBtn.ant-btn {
  font-weight: 600 !important;
  font-size: 14px !important;
  border-radius: 0px !important;
}

.NB > .ant-btn {
  font-weight: 600 !important;
  font-size: 14px !important;
  border-radius: 0px !important;
}

.ant-message-notice-content {
  border-radius: 8px !important;
}

.ant-input-affix-wrapper > input.ant-input {
  padding-left: 8px !important;
}

.ant-modal-footer {
  display: none !important;
}
.ant-modal-body {
  padding: 0;
}

.drag > .ant-modal-content {
  background-color: #000 !important;
  border-radius: 10px;
  overflow: hidden;
}

.dial > .ant-modal-content {
  border-radius: 10px;
  overflow: hidden;
}

.ant-modal-header {
  display: none !important;
}

.ant-modal-close-x {
  color: #fff !important;
  display: none !important;
  justify-content: center !important;
  align-items: center !important;
  width: 24px !important;
  height: 24px !important;
  border-bottom-left-radius: 10px !important;
  background-color: black !important;
}

.ant-notification-notice-closable .ant-notification-notice-message {
  padding: 0 !important;
}

.ant-notification-notice-message {
  margin: 0 !important;
}

.ant-notification-notice {
  padding: 0 !important;
  border-radius: 10px !important;
}

.anticon.anticon-close.ant-notification-notice-close-icon {
}

.nBtn {
  border-radius: 50px;
  background: #1677ff;
  /* box-shadow: 11px 11px 22px #1054b5, -11px -11px 22px #1c9aff; */
  font-weight: 600 !important;
}

.wBtn {
  border-radius: 50px;
  /* background: #e0e0e0; */
  /* box-shadow: 11px 22px 22px #9f9f9f; */
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.zBtn {
  border-radius: 50px;
  background: #e0e0e0;
  box-shadow: 10px 10px 20px #bebebe, -10px -10px 20px #ffffff;
}

.nuy {
  border-radius: 53px;
  background: #e0e0e0;
  box-shadow: inset 5px 5px 10px #9f9f9f, inset -5px -5px 10px #ffffff;
}

.ant-btn {
  font-weight: 700 !important;
}

:where(.css-dev-only-do-not-override-stni74).ant-btn.ant-btn-lg {
  font-weight: 700 !important;
}

.ant-input-affix-wrapper > input.ant-input {
  font-weight: 700 !important;
}

.ant-modal-content {
  padding: 0 !important;
  margin-bottom: -10px !important;
}
